import React, { useContext, useState } from 'react'
import MyContext from "./Context/MyContext";

function MessageBox() {
    const context = useContext (MyContext);
    const {sendmessage,setdata,data,chat}=context;
    const senddata = (e)=>{
        e.preventDefault()
        sendmessage()
      }

      
  return (
<>
<div className='overflow-y-scroll    h-[85%] '>
{chat.map((element)=>{
  return <div style={element.style} key={element.index}>
  <p className='text-white border-white overflow-auto  w-full  px-2 font-semibold font-Roboto rounded-md  mb-1'>{element.message}</p> 
  </div>})}
</div>

<div className=' h-[15%]  w-[19vw] p-1 flex gap-1  mt-2 items-center'>
<input value={data} onChange={(e)=>{setdata(e.target.value)}} className='  w-full text-white font-Roboto bg-transparent outline-none' placeholder='Enter message' /> 
<button  onClick={senddata} className='text-white border rounded-md px-1'>Send</button>
</div>
</>
  )
}

export default MessageBox