import React, { useContext, useEffect, useState} from 'react';
import MyContext from "./Context/MyContext";
import ReactPlayer from 'react-player/lazy'
import screenfull from 'screenfull';
import { useNavigate } from 'react-router-dom';
import copy from 'clipboard-copy';
import CallBox from './CallBox';
import VideoBox from './VideoBox';

function Main() {
  // const navigator = useNavigate();
  const context = useContext (MyContext);
  const {selfid,setconnectionformessage}=context;
  const [mode, setmode] = useState("Full Screen")
  let a=1;


const onClick = async ()=>{
  try {
    await copy(selfid);
    alert('Text successfully copied to clipboard');
  } catch (error) {
    console.error('Unable to copy to clipboard:', error);
  }}

const onClickFullScreen = (e)=>{
  e.preventDefault();
if(a===1){
  screenfull.request()
  setmode("esc to exit full screen")
  a=0
}else if(a===0){
  setmode("Full Screen")
  a=1
}}



  return (
    <div  className="h-screen w-screen flex font-Roboto">
      <div className=" w-[80%] flex-col relative bg-black">
          <div className='h-[5%]  w-full bg-[#776B5D] px-5 py-1 flex items-center gap-3 justify-between'>
         <div className='flex items-center gap-3'>
         <button onClick={onClick} className='border px-2 text-sm rounded-xl font-semibold text-[#F5F5F5] border-[#F5F5F5]'>Room id: {selfid? selfid : "loading..."}</button> |
          <p className='text-xs text-white'>Send your room id to your friend and then fun with your friend</p>
         </div>
          <button onClick={setconnectionformessage} className='border px-2 text-sm rounded-xl  font-semibold  text-[#F5F5F5] border-[#F5F5F5]'>connect</button> 
          <button onClick={onClickFullScreen} className='border px-2 text-sm rounded-xl  font-semibold  text-[#F5F5F5] border-[#F5F5F5]'>{mode}</button> 
          </div>
    <div className='h-[95%]'>
      {/* <VideoBox/> */}
      </div>
    </div>

      {/* bg-[#DE8971] */}
      <div className='h-full w-[20%] flex justify-end'>
      <CallBox/>
      </div>

     
    </div>
  );
}

export default Main;
