import React, { useEffect, useRef, useState } from 'react';
import MyContext from './MyContext';
import {  Peer } from "peerjs";
import cors from"cors";
import { connect } from 'mongoose';
function Context(props) {

cors()

const [selfid, setselfid] = useState("");
const [theirid, settheirid] = useState("");
const selfstream = useRef([]);
const theirstream = useRef([]);
const [chat, setchat] = useState([]);
const [alertmessage, setalertmessage] = useState("")
const [data, setdata] = useState("")
const connRef = useRef(null);
const [style, setstyle] = useState("")
const peer = new Peer();


//connecting to peer
const connectpeer =async ()=>{
  // console.log("getting id")
  peer.on("open",(id)=>{
    setselfid(id)
    // console.log("got sucess",id)
  })
}

//listen for connection event
peer.on("connection",(connection)=>{
  // console.log("incoming connection request");

  connection.on("data",(receivedData)=>{
    // console.log(receivedData)
    setchat((prevChat) => [...prevChat,receivedData]);
  })

  connRef.current=connection;
})


//calling to a person 
const startcall  =async ()=>{
  try{
    var mediaStream = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
    await mediaStream({video:true,audio:true}, (stream)=>{
      var call = peer.call(theirid,stream);    
      selfstream.current.srcObject = stream ;
      call.on('stream',(stream)=>{
        theirstream.current.srcObject = stream
      })
    })
    setconnectionformessage()
    
  }catch(error){
  console.log(error)
}}

  // Answering a call 

  peer.on('call',async (call)=>{
    // console.log("incoming call")
    var mediaStream = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
    await mediaStream({video:true,audio:true}, (stream)=>{
    call.answer(stream)
    selfstream.current.srcObject = stream
    })
    // console.log("call recived")
    call.on('stream',(stream)=>{
      theirstream.current.srcObject = stream
    })
})

const setconnectionformessage = ()=>{
  const conn = peer.connect(theirid);

  //listening for message
  conn.on("data",(receivedData)=>{
    // console.log(receivedData)
    setchat((prevChat) => [...prevChat,receivedData]);
    // console.log(chat)
  })

  connRef.current= conn;
}

const sendmessage = async ()=>{
  // await setstyle
  if(connRef.current){
    setchat((prevChat) => [...prevChat,{"id":selfid,"message":data,"style":{textAlign:"right"}}]);
    connRef.current.send({"id":selfid,"message":data,"style":{textAlign:"left"}})
  }
}
    

      return (
        <MyContext.Provider  value={{connectpeer,selfid,peer,theirid,settheirid,peer,selfstream,theirstream,startcall,alertmessage,setalertmessage,chat,setdata,sendmessage,data,setconnectionformessage}}>
        {props.children}
        </MyContext.Provider>
      )
    }
    
    export default Context;
    